<!-- 食品安全 -->
<template>
	<page-box>
		<!-- 左边 -->
		<div class="c_left clear" slot="left">
			<div class="left_1 clear">
				<todayWarning 
					:static="static.left0101"
				></todayWarning>
				<deploy
					:static="static.left0102"
				></deploy>
			</div>
			<div class="left_2">
				<div class="left_2_1">
					<warn-sum
						:num="static.left02.num"
						:static="static.left02"
					></warn-sum>
				</div>
        <div class="left_2_2">
          <shopAlert
						:static="static.left02.left0202"
          ></shopAlert>
        </div>
			</div>
			<div class="left_3">
        <sensorAlert
					:static="static.left03"
        ></sensorAlert>
			</div>
		</div>
		<!-- 中间 -->
		<div class="c_center clear" slot="center">
			<div class="center_1">
				<iframe :src="static.centerTop01.iframe" class="cont"></iframe>
			</div>
			<div class="center_2">
        <div class="middleLeft">{{static.center01.title}}</div>
        <img :src="'static/img/'+static.center01.pic" alt="">
			</div>
			<div class="center_3">
        <ul>
          <li 
          	v-for="item in static.center02.picList"
          ><img :src="'static/img/'+item" alt=""></li>
        </ul>
			</div>
		</div>
		<!-- 右边 -->
		<div class="c_right clear" slot="right">
			<div class="right_1">
        <warn-occur-time
          :list="static.right01.dataList"
          :static="static.right01"
        ></warn-occur-time>
			</div>
			<div class="right_2">
        <warningTop8
					:static="static.right02"
        ></warningTop8>
			</div>
			<div class="right_3">
        <div class="com_title">
          {{static.right03.title}}
        </div>
        <table-t02 class="r_3_table"
			     :list="static.right03.tdList"
			     :static="static.right03"
        ></table-t02>
			</div>
		</div>
	</page-box>
</template>

<script>

import PageBox from '@/modules/page-box.vue';
import deploy from '@/components/deploy.vue';
import tableOrderDay from '@/components/tableOrderDay.vue';

// 消防告警
import warnSum from '@/components/warnSum.vue';
import shopAlert from '@/components/shopAlert.vue';
import warnToday from '@/components/warnToday.vue';
import sensorAlert from '@/components/sensorAlert.vue';


import warnOccurArea from '@/components/warnOccurArea.vue';
import warningTop8 from '@/components/warningTop8.vue';

import warnOccurTime from '@/components/warnOccurTime.vue';

import tableT02 from '@/components/tableT02.vue';
import todayWarning from '@/components/todayWarning.vue';

// 页面数据
import pageJson from '@/../static/pageJson/food.json';

// js
import {insertScript} from '@/assets/js/assets.com.js';

export default {
	name: '',
	components: {
		PageBox,
		todayWarning, deploy, warnSum, shopAlert, warnToday, tableOrderDay,sensorAlert,
		warnOccurTime,warningTop8,
		warnOccurArea, tableT02
	},
	data () {
		return {
      static: pageJson,
      timer: ''
		}
	},
	mounted(){
		var timer;
		var timerI = 0;
		var _vue = this;
		_vue.queryData( timerI );
		this.timer = setInterval(function () {
			console.log( 'food:', timerI )
		  _vue.queryData( timerI );
		  timerI++;
		  timerI%=5;
		}, 30000)
	},
	beforeRouteLeave (to, from, next){
		clearInterval( this.timer );
		next();
	},
	methods: {
		queryData(index){
			var _vue = this;
			
			insertScript( `./static/data/food/data${index}.js` );
			// $('#myScript').html( `'<script data-type=static src=""><\/script>'` );
			_vue.static = $.extend(true, {}, pageJson, staticData );
		}
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
	div{
		box-sizing: border-box;
	}
	// 坐边
	.c_left{
		padding-left: 40px;
		padding-bottom: 15px;
		>div{
			float: left;
		}
		.left_all(){
			overflow: hidden;
			box-sizing: border-box;
		}
		.left_1{
			.left_all();
			width: 100%;
			height: 265px;
			margin-bottom: 15px;
			border-radius: 8px;
			border: solid 1px rgba(56, 174, 225, 0.71);
		}
		.left_2{
			.left_all();
			width: 400px;
			height: 675px;
			border: solid 1px rgba(56, 174, 225, 0.71);
			border-radius: 8px;
			.left_2_2{
				height: 415px;
				border-radius: 10px;
        width: 100%;
        background-color: rgba(6, 21, 40, 0.86);
			}
		}
		.left_3{
			.left_all();
			width: 795px;
      margin-left: 5px;
      height: 675px;
      border: solid 1px rgba(56, 174, 225, 0.71);
      border-radius: 8px;
		}
	}
	// 中间
	.c_center{
		padding: 0 20px 0 20px;
		>div{
			float: left;
		}
		.center_1{
			width: 1390px;
			height: 600px;
			margin-bottom: 20px;
			border-radius: 8px;
			// border: solid 1px rgba(56, 174, 225, 0.71);
		}
		.center_2{
      width: 450px;
      height: 332px;
			border-radius: 8px;
      margin:0 5px;
      border:1px solid rgba(56, 174, 225, 0.71);
      .middleLeft{
        height: 100px;
        line-height: 100px;
        font-size: 30px;
        text-align: center;
        color: #00fff0;
      }
      img{
        width: 420px;
        height: 220px;
        margin-left: 15px;
        text-align: center;
      }
		}
		.center_3{
			width: 930px;
			height: 330px;
			border-radius: 8px;
			border:1px solid rgba(56, 174, 225, 0.71);
      ul{
        li{
          float: left;
          width: 220px;
          height: 150px;
          margin-top: 15px;
          img{
            margin-top: 5px;
            width: 200px;
            height: 130px;
            margin-left: 20px;
          }
        }
      }
		}
	}
	// 右边
	.c_right{
		height: 100%;
		width: 100%;
		padding-right: 30px;
		padding-bottom: 15px;
		>div{
			float: left;
		}
		.right_1{
			width: 570px;
			height: 510px;
			margin-bottom: 15px;
			border-radius: 8px;
			border: solid 1px rgba(56, 174, 225, 0.25);
		}
		.right_2{
			width: 555px;
			height: 510px;
			margin-bottom: 15px;
			margin-left: 15px;
			border-radius: 8px;
		}
		.right_3{
			width: 1140px;
			height: 430px;
			border-radius: 8px;
			border: solid 1px rgba(56, 174, 225, 0.25);
		}
	}

	// 左边内容
	.left_1{
		>div{
			float: left;
		}
	}
	.left_2_2{
    float: left;
	}
	.center_2{
		overflow: hidden;
	}
	.com_title{
		font-size: 30px;
		line-height: 60px;
		letter-spacing: 0px;
		color: #00fff0;
		padding-left: 40px;
	}

	// 中间
	.center_1{

		position: relative;
		img{
			position: absolute;
			left: 0;
			right: 0;
			height: 100%;
			width: 100%;
		}
	}

</style>



