<!-- 商户告警 -->
<template>
	<div class="shopAlert">
      <div class="title">{{static.title}}</div>
      <table>
        <thead>
          <tr>
            <th>商户</th>
            <th>告警次数</th>
          </tr>
        </thead>
        <tbody>
          <tr>
            <td rowspan="2">{{static.data1.name}}</td>
            <td>{{static.data1.desc1}}</td>
          </tr>
          <tr>
            <td>{{static.data1.desc2}}</td>
          </tr>
        </tbody>
      </table>
      <div class="allNumber">
        <img src="../../static/img/eatingFood.png" alt="">
        <span class="totle">{{static.data2.name}}</span>
        <p>{{static.data2.num}}<span>{{static.data2.desc}}</span></p>
      </div>
	</div>
</template>

<script>
export default {
	name: '',
	props: {
		list:{
			default: function (){

			}
		},
		static: {
			default(){
				return {
					"title": "商户告警",
          "data1": {
            "name": "老丰阁食品加工",
            "desc1": "温度123次",
            "desc2": "湿度123次"
          },
          "data2": {
            "name": "街道餐厅总数",
            "num": "91",
            "desc": "个"
          }
				}
			}
		}
	},
	data () {
		return {

		}
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
  .shopAlert {
    width: 395px;
    height: 415px;
    background-color: rgba(6, 21, 40, 0.86);
    opacity: 0.86;
    .title {
      height: 75px;
      line-height: 75px;
      font-size: 24px;
      padding-left: 30px;
      color: #00f4e6;
      border: 2px solid rgba(25, 80, 112, 0.67);
      border-bottom: none;
    }
    table {
      width: 100%;
      border-collapse: collapse;
      text-align: center;
      vertical-align: middle;
      tr{
        height: 50px;
        line-height: 50px;
        td{
          border: 2px solid rgba(25, 80, 112, 0.67);
          font-weight: 200;
          font-size: 20px;
          color: #eee;
        }
        th{
          border: 2px solid rgba(25, 80, 112, 0.67);
          font-weight: 200;
          font-size: 20px;
          background-color: rgba(31, 79, 106, 0.53);
          color: #eee;
        }
      }
    }
    .allNumber{
      position: relative;
      img {
        position: absolute;
        width: 96px;
        height: 95px;
        left: 35px;
        top: 40px;
      }
      span.totle{
        position: absolute;
        display: inline-block;
        font-size: 20px;
        font-weight: normal;
        font-stretch: normal;
        line-height: 40px;
        color: #ffffff;
        left: 160px;
        top: 30px;
      }
      p{
        position: absolute;
        display: inline-block;
        font-size: 60px;
        color: #ff817a;
        left: 180px;
        top: 65px;
        span{
          font-size: 30px;
        }
      }
    }
  }
</style>



