<!-- 传感器告警 -->
<template>
	<div class="sensorAlert">
    <div class="title">{{static.title}}</div>
    <ul>
      <li v-for="items in static.dataList">
        <img :src="'static/img/'+items.pic" alt="">
        <span class="name">{{items.name}}</span>
        <span class="number">{{items.number}}</span>
      </li>
    </ul>
	</div>
</template>

<script>
export default {
	name: '',
	props: {
		list:{
      default: function() {
        return ''
      }
		},
		static: {
			default(){
				return {
          "title": "传感器告警",
          "datalist": [
            {
              "pic": "food_temp.png",
              "name": "温度",
              "number": "35次"
            }, {
              "pic": "food_wetness.png",
              "name": "湿度",
              "number": "16次"
            }, {
              "pic": "food_ foreign_matter.png",
              "name": "异物",
              "number": "23次"
            }, {
              "pic": "food_clothing.png",
              "name": "着装不规范",
              "number": "59次"
            }
          ]
        }
			}
		}
	},
	data () {
		return {

		}
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
  .sensorAlert {
    width: 798px;
    height: 676px;
    padding: 30px;
    background-color: rgba(2, 35, 92, 0.16);
    .title{
      font-size: 30px;
      color: #00fff0;
    }
    ul{
      height: 600px;
      li{
        float: left;
        position: relative;
        width: 50%;
        height: 50%;
        img{
          position: absolute;
          top: 75px;
          left: 50px;
        }
        span.name{
          display: inline-block;
          position: absolute;
          top: 230px;
          left:0;
          font-size: 24px;
          color: #ffffff;
          width: 220px;
          text-align: center;
        }
        span.number{
          display: inline-block;
          font-size: 48px;
          position: absolute;
          top: 120px;
          left:130px;
          color: #69c6ff;
          min-width: 300px;
          text-align: center;
        }
      }
    }
  }
</style>



